


import "./index.scss";
import {Button,WhiteSpace,NoticeBar,Accordion,List} from "antd-mobile";
import Head  from "~/components/Head"
import {connect} from "react-redux";
import { countAdd } from "../../actions";

import demo from "~/mobx/demo";
console.log(demo);
import {observer} from "mobx-react"

@connect(
    state=>(
        {
            count:state.getIn(['data','count']),
            word:state.getIn(['data','word'])
        }
    )
)
@observer
export default class Home extends Component{

    change=e=>{
        demo.changeMsg(e.target.value);
        
    }
    render(){
        const {
            count,
            word,
            dispatch
        } = this.props;
        const {
            num,
            msg,
            number,
            countadd,
            countDesc
        } = demo;
        return (
            <div className="indexview">
                <Head title="首页"></Head>
                <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
                  wuhan1906 daydayup!!! 天道酬勤  努力周天写代码!!!! 加油...
                </NoticeBar>
                <div>
                    <Button type="primary">primary</Button><WhiteSpace />
                    <Button type="primary" disabled>primary disabled</Button><WhiteSpace />
                    <Button type="warning">warning</Button><WhiteSpace />
                    <Button type="warning" disabled>warning disabled</Button><WhiteSpace />
                    <p>
                        <input type="text" onChange={this.change}/>
                    </p>
                </div>
                <div>
                <Accordion defaultActiveKey="0" className="my-accordion" onChange={this.onChange}>
                    <Accordion.Panel header="Title 1">
                        <List className="my-list">
                        <List.Item onClick={()=>dispatch(countAdd(66))}>content 1 ==== {count}</List.Item>
                        <List.Item onClick={()=>countDesc(70)}>{word} / {number}</List.Item>
                        <List.Item onClick={countadd}> {num} / {msg}</List.Item>
                        </List>
                    </Accordion.Panel>
                    <Accordion.Panel header="Title 2" className="pad">this is panel content2 or other</Accordion.Panel>
                    <Accordion.Panel header="Title 3" className="pad">
                        text text text text text text text text text text text text text text text
                    </Accordion.Panel>
                    </Accordion>
                </div>
            </div>
        )
    }
}

