

import {observable,action,computed,autorun }  from "mobx"


class Demo{
    @observable num = 2019;  // 可被观察数据
    @observable msg = "Are you OK";
    
    @action countadd=()=>{  // 修改 state 
        this.num++;
    }

    @action countDesc=(num)=>{
        this.num-=num;
    }

    @action changeMsg=msg=>{
        this.msg = msg;
    }

    @computed get number(){
        return this.num + 2000;
    }
}


export default new Demo();


